.sidebar {
  position: sticky;
  top: 80px;
  flex: 0.2;
  border-radius: 10px;
  height: fit-content;
  text-align: center;

  & > img {
    margin-bottom: -40px;
    width: 100%;
    height: 60px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    object-fit: cover;
  }

  & .sidebar__avatar {
    margin-bottom: 10px;
  }

  & .sidebar__top {
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid lightgray;
    border-bottom: 0;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    background-color: white;
    padding: 10px;

    & > h4 {
      color: gray;
      font-size: 12px;
    }

    & > h2 {
      font-size: 10px;
    }
  }

  & .sidebar__stats {
    padding: 10px;
    margin-bottom: 10px;
    border: 1px solid lightgray;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    background-color: white;

    & .sidebar__stat {
      margin-top: 10px;
      display: flex;
      justify-content: space-between;

      & > p {
        color: gray;
        font-size: 13px;
        font-weight: 600;
      }

      & .sidebar__stat-number {
        font-weight: bold;
        color: #0a66c2;
      }
    }
  }

  & .sidebar__bottom {
    text-align: left;
    padding: 10px;
    border: 1px solid lightgray;
    background-color: white;
    border-radius: 10px;
    margin-top: 10px;

    & > p {
      font-size: 13px;
      padding-bottom: 10px;
    }

    & .sidebar__recent-item {
      display: flex;
      font-size: 13px;
      color: gray;
      font-weight: bolder;
      cursor: pointer;
      margin-bottom: 5px;
      padding: 5px;
      transition: ease 1s;

      &:hover {
        background-color: whitesmoke;
        border-radius: 10px;
        cursor: pointer;
        color: black;
      }
    }

    & .sidebar__hash {
      margin-right: 10px;
      margin-left: 5px;
    }
  }
}
