.header-options {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 20px;
  color: gray;
  cursor: pointer;
  transition: ease 0.5s;

  &:hover {
    color: black;
  }

  & .header-option__title {
    font-size: 12px;
    font-weight: 400;
  }

  & .header-option__icon {
    object-fit: contain;
    height: 25px !important;
    width: 25px !important;
  }
}
